import React, { Component  } from 'react';
import './App.css';
import { BrowserRouter, Switch, Route, Redirect, HashRouter} from 'react-router-dom'
import Shares from './shares/Shares'
import Charts2 from './shares/Charts2'
import Timers from './timers/Timers'
import Gasto from './caja/Gasto'
import Random from './photos/Random'
import Orphans from './photos/Orphans'
import Today from './photos/Today'
import { Table, Button, Navbar, Nav, NavDropdown} from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';

class App extends Component {
  state = { redirect: false };
  //console.log("load");

  redirect = (e) => {
    this.setState({ url: e.target.value, redirect: true});
  }

  renderRedirect = () => {
    if(this.state.redirect){
      //this.setState({redurect: false});
      return <Redirect to={this.state.url} />
    }
  }

  menu = () => {
    return (<div><br />
      <Table borderless>
        <tbody>
          <tr>
            <td>
              <Button size="lg" onClick={this.redirect} value="/timers" variant="primary" className="btn btn-primary">
                Timers
              </Button>&nbsp;
            </td>
            <td>
              <Button size="lg" onClick={this.redirect} value="caja2/gasto" variant="primary" className="btn btn-primary">
                Gasto
              </Button>
            </td>
            <td>
              <Button size="lg" onClick={this.redirect} value="/photos" variant="primary" className="btn btn-primary">
                Photos
              </Button>
            </td>
          </tr>
          <tr>
            <td>
              <Button size="lg" onClick={this.redirect} value="/photos/today" variant="primary" className="btn btn-primary">
                Photos Today
              </Button>&nbsp;
            </td>
            <td>
              <Button size="lg" onClick={this.redirect} value="/photos/orphans" variant="primary" className="btn btn-primary">
                Orphan Photos
              </Button>
            </td>
            <td>
              <Button size="lg" onClick={this.redirect} value="caja2/gasto" variant="primary" className="btn btn-primary">
                Gasto
              </Button>
            </td>
          </tr>
        </tbody>
      </Table>
    </div>
  );
  }

  routes = [
    //{exact: true,  path: '/',               component: this.hrouter},
    {exact: true,  path: '/home',           component: this.menu},
    {exact: true,  path: '/react-mablor',   component: this.menu},

    {exact: false, path: '/shares',         component: Shares},
    {exact: false, path: '/shares/charts2', component: Charts2},

    {exact: false, path: '/timers',         component: Timers},

    {exact: true,  path: '/photos',         component: Random},
    {exact: true,  path: '/photos/random',  component: Random},
    {exact: true,  path: '/photos/orphans', component: Orphans},
    {exact: false, path: '/photos/today',   component: Today},

    {exact: false, path: '/caja2/gasto',    component: Gasto}
  ];

  hRouter = () => {
    return(
      <HashRouter>
        <Switch>
          <Route path='/' component={this.menu} exact={true} key='root' />
          {this.routes.map( r => {return(
            <Route path={r.path} component={r.component} exact={r.exact} key={r.path} />
          )})}
        </Switch>
      </HashRouter>
    );
  }

  navBar = () => {
    return (
      <Navbar expand="lg" variant="dark" bg="dark">
        <Navbar.Brand href="#home">Mablor</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="mr-auto">
            <Nav.Link href="#home">Home</Nav.Link>
            <Nav.Link href="#timers">Timers</Nav.Link>
            <Nav.Link href="#photos">Photos</Nav.Link>
            <NavDropdown title="Photos" id="basic-nav-dropdown">
              <NavDropdown.Item href="#photos/random">Random</NavDropdown.Item>
              <NavDropdown.Item href="#photos/orphans">Orphans</NavDropdown.Item>
              <NavDropdown.Item href="#photos/today">Today</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
            </NavDropdown>
            <NavDropdown title="Caja" id="basic-nav-dropdown">
              <NavDropdown.Item href="#caja2/gasto">Gasto</NavDropdown.Item>
              <NavDropdown.Item href="#caja2/gasto">Gasto</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
            </NavDropdown>
            <NavDropdown title="Shares" id="basic-nav-dropdown">
              <NavDropdown.Item>Charts</NavDropdown.Item>
              <NavDropdown.Item>Another action</NavDropdown.Item>
              <NavDropdown.Item>Something</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
            </NavDropdown>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    );
  }

  render() {
    //console.log('====',window.location);
    const navB = (window.location.pathname==="/photos"?'':this.navBar());
    //const navB = this.navBar();
    return(
      <div className="App">
        <BrowserRouter>
          {this.renderRedirect()}
          {navB}
          <Switch>
            <Route path='/' component={this.hRouter} exact={true} key='root' />
            {this.routes.map( r => {return(
              <Route path={r.path} component={r.component} exact={r.exact} key={r.path} />
            )})}
          </Switch>
        </BrowserRouter>
      </div>
    );
  }
}

export default App;
