import React, {Component} from 'react';
import axios from 'axios';
//import DatePicker from 'react-date-picker';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Table, Button, InputGroup, FormControl} from 'react-bootstrap';

class Gasto extends Component{

  state = {
    ready: false,
    cuentas: [],
    cuentasOptions: [{label:"A", value:1}, {label:"B", value:2}],
    ASelected: 2,
    DeSelected: 2,
    amount: "",
    detalle: "",
    mydate: '2020-01-01',
    saving: false
  };

  componentDidMount() {
    //console.log('componentDidMount location', window.location);
    axios.get(this.serviceURL()+"/caja-api/cuentas",
      {headers: {'Access-Control-Allow-Origin': ' *'}})
      .then(this.init)
      .catch(function(error) {
        console.log(error);
      });
  }

  init = (response) => {
      const cuentas = response.data;
      //this.setState( {cuentas: {...cuentas}});
      const cuentasOptions = [];
      cuentas.forEach( c => {
        cuentasOptions.push({ label: c.detalle, value: c.id});
      })
      //this.setState( {cuentasOptions: cuentasOptions});
      //this.setState( {ASelected: "2"});
      //this.setState( {DeSelected: "4"});
      const today = new Date();
      const s = today.toISOString();
      //this.setState( {mydate: s.substr(0,10)});
      this.setState( {
        mydate: s.substr(0,10),
        DeSelected: 2,
        ASelected: 1,
        amount: "",
        detalle: "",
        cuentasOptions: cuentasOptions,
        cuentas: {...cuentas},
        ready: true
      } );
      //console.log('init cuentas = ',cuentas)
      //console.log('init today = ',s)
  };

  serviceURL = () => {
    //console.log('ENV:', process.env);
    const url = process.env.NODE_ENV==="development" ? window.location.protocol+'//'+window.location.hostname+':8000' : "";
    //console.log('URL:', url);
    return url;
  };

  dateChange = e => {
    //console.log('change = ', e.target.value);
    this.setState( {mydate: e.target.value});
  };

  deChange = e => {
    //console.log('change = ', e.target.value);
    this.setState({ DeSelected: parseInt(e.target.value, 10) });
  };

  aChange = e => {
    //console.log('change = ', e.target.value);
    this.setState({ ASelected: parseInt(e.target.value, 10) });
  };

  amountChange = e => {
    //console.log('change = ', e.target.value);
    this.setState({ amount: e.target.value });
  };

  detalleChange = e => {
    //console.log('change = ', e.target.value);
    this.setState({ detalle: e.target.value });
  };

  getCuentas = defaultValue => {
    var ctas = this.state.cuentasOptions;
    var r = [];
    ctas.forEach( c =>{
      if(c.value===defaultValue){
        r.push(<option key={c.value} value={c.value}>{c.label}</option>);
      }else{
        r.push(<option key={c.value} value={c.value}>{c.label}</option>);
      }
    });
    return r;
  };

  save = e => {
    //console.log('save');
    this.setState({saving:true});
    axios.put(this.serviceURL()+'/caja-api/renglones',{
      headers: {'Access-Control-Allow-Origin': ' *'},
      data: {
        date: this.state.mydate,
        amount: this.state.amount,
        de: this.state.DeSelected,
        a: this.state.ASelected,
        detalle: this.state.detalle
      }
    }).then(this.saved).catch(function(error){console.log(error);alert(error,[],{})});
  };

  saved = e => {
    //console.log('save');
    //console.log(e);
    //alert('Hi',[],{});
    this.setState({
      amount: "",
      detalle: "",
      saving:false
    });
  }

  clickTag = (e) => {
    //console.log(e.target);
    this.setState({detalle: e.target.value});
  }

  btag = (tag) => {
    const style3 = {width: "100px", height: "25px", padding: "0px"};
    return(
      <Button onClick={this.clickTag} value={tag} style={style3} variant="primary" className="btn btn-primary">
        {tag}
      </Button>
    )
  }

  rowtag = (t1, t2) => {
    const style5 = {padding: "1px"};
    return(
      <div style={style5}>
        {this.btag(t1)}&nbsp;
        {this.btag(t2)}<br />
      </div>
    )
  }

  celltag = () => {
    const style2 = {padding: "50px"};
    const t = ['aaa','bbb','ccc','ddd','eee','fff','ggg','hhh','iii','jjj',
                'kkk','lll','mmm','nnn','ooo','ppp'];
    return(
      <td style={style2}>
        {this.rowtag(t[0],t[1])}
        {this.rowtag(t[2],t[3])}
        {this.rowtag(t[4],t[5])}
        {this.rowtag(t[6],t[7])}
        {this.rowtag(t[8],t[9])}
        {this.rowtag(t[10],t[11])}
        {this.rowtag(t[12],t[13])}
        {this.rowtag(t[14],t[15])}
      </td>
    )
  }

  clickNum = (e) => {
    const oldAmount = this.state.amount;
    const c = e.target.value;
    var newAmount = oldAmount + c;
    if(c === '<<'){
      newAmount = oldAmount.substr(0,oldAmount.length-1)
    }
    this.setState({amount: newAmount})
  }

  bnum = (num) => {
    const style1 = {width: "50px", height: "50px", padding: "0px"};
    return(
      <Button onClick={this.clickNum.bind(num)} value={num} style={style1} size="lg" variant="primary" className="btn btn-primary">
        {num}
      </Button>
    )
  }

  rownum = (n1, n2, n3) => {
    const style4 = {padding: "2px"};
    return(
      <div style={style4}>
        {this.bnum(n1)}&nbsp;
        {this.bnum(n2)}&nbsp;
        {this.bnum(n3)}<br />
      </div>
    )
  }

  cellnum = () => {
    const style2 = {padding: "50px"};
    return(
      <td style={style2}>
        {this.rownum(1,2,3)}
        {this.rownum(4,5,6)}
        {this.rownum(7,8,9)}
        {this.rownum(".",0,"<<")}
      </td>
    )
  }

  render(){
    const size = "sm" //sm md lg
    //const report_style = { position: 'static', float: 'left', width: '100%' }
    //console.log('>>>>> render state', this.state);
    return (
      <div className="container">
          <div>&nbsp;</div>
          <InputGroup size={size} className="mb-3">
            <InputGroup.Prepend>
              <InputGroup.Text id="basic-addon1">Fecha:</InputGroup.Text>
            </InputGroup.Prepend>
            <FormControl type="date" placeholder="" aria-label="Fecha" aria-describedby="basic-addon1"
              value={this.state.mydate}
              onChange={this.dateChange}
            />
          </InputGroup>
          <InputGroup size={size} className="mb-3">
            <InputGroup.Prepend>
              <InputGroup.Text id="basic-addon1">Importe:</InputGroup.Text>
            </InputGroup.Prepend>
            <FormControl value={this.state.amount} onChange={this.amountChange} autoFocus aria-label="amount" aria-describedby="basic-addon1"/>
          </InputGroup>
          <InputGroup size={size} className="mb-3">
            <InputGroup.Prepend>
              <InputGroup.Text id="basic-addon1">Detalle:</InputGroup.Text>
            </InputGroup.Prepend>
            <FormControl value={this.state.detalle} onChange={this.detalleChange} aria-label="" aria-describedby="basic-addon1"/>
          </InputGroup>
          <InputGroup size={size} className="mb-3">
            <InputGroup.Prepend>
              <InputGroup.Text id="basic-addon1">De:</InputGroup.Text>
            </InputGroup.Prepend>
            <FormControl as="select" value={this.state.DeSelected} onChange={this.deChange} aria-describedby="basic-addon1">
              {this.getCuentas(0)}
            </FormControl>
          </InputGroup>
          <InputGroup size={size} className="mb-3">
            <InputGroup.Prepend>
              <InputGroup.Text id="basic-addon1">A:</InputGroup.Text>
            </InputGroup.Prepend>
            <FormControl as="select" value={this.state.ASelected} onChange={this.aChange} aria-describedby="basic-addon1">
              {this.getCuentas(0)}
            </FormControl>
          </InputGroup>
  				<Button size={size} disabled={this.state.saving} block variant="primary" id="grabar" onClick={this.save} value="grabar" name="grabar" className="btn btn-primary">
            Save
          </Button>
          <Table borderless>
            <tbody>
              <tr>
                {this.cellnum()}
                {this.celltag()}
              </tr>
            </tbody>
          </Table>

      </div>

    );
  }
}

export default Gasto;
