import React, {Component} from 'react';
//import ReactSpeedometer from 'react-d3-speedometer';
import axios from 'axios'
import Chart from 'react-google-charts';
//import {Row, Col} from 'antd';
import {Button} from 'react-bootstrap';

class Timers extends Component{

  state = {
    ready: false,
    timers: {}
  };

  tds = 0;

  serviceURL = () => {
    //console.log('ENV:', process.env);
    const url = process.env.NODE_ENV==="development" ? window.location.protocol+'//'+window.location.hostname+':8000' : "";
    //console.log('URL:', url);
    return url;
  };

  update = (response) => {
    //console.log('response', response);
    const timers = response.data;
    //console.log('timers2', timers);
    this.setState( {timers: {...timers} } );
    this.setState( {ready: true} );
  };

  update1 = (response) => {
    //console.log('response', response);
    //const timers = response.data;
    //console.log('timers2', timers);
    //this.setState( {timers: {...timers} } );
    //this.setState( {ready: true} );
  };

  refresh = () => {
    //console.log('location', window.location);
    axios.get(this.serviceURL()+"/timers-api/list",
      {headers: {'Access-Control-Allow-Origin': ' *'}})
      .then(this.update)
      .catch(function(error) {
        console.log(error);
      });
  }

  componentDidMount() {
    const intervalID = setInterval(() => {this.setState({intervalID});}, 3000);
    this.refresh();
  }

  onClickTimer = (label, ticks, e) => {
    //console.log('click label', label, 'ticks', ticks, 'e', e);
    //this.setState( {ready: false});
    //const tim = this.state.timers[label];
    let timers = this.state.timers;
    let onff = (timers[label]['state'] === 'on' ? 'off' : 'on');
    timers[label]['state'] = onff;
    timers[label]['started'] = ticks;
    this.setState({timers: {...timers}})
    axios.put(this.serviceURL()+'/timers-api/switch/'+label+'/'+ticks,
      {headers: {'Access-Control-Allow-Origin': ' *'}})
      .then(this.update1)
      .catch(function(error) {
        console.log(error);
      });
  };

  move = (label, op, e) => {
    //console.log('click label', label, 'op', op, 'e', e);
    //this.setState( {ready: false});
    let timers = this.state.timers;
    //console.log(timers[label]);
    let inc = (op === '+' ? 1800000 : -1800000);
    timers[label]['started'] = Math.round(timers[label]['started'])+inc; // / timers[label]['units'];
    //console.log(timers[label]);
    //alert(timers[label]);
    this.setState({timers: {...timers}})
    //const tim = this.state.timers[label];
    axios.put(this.serviceURL()+'/timers-api/move/'+label+'/'+inc,//30 min
      {headers: {'Access-Control-Allow-Origin': ' *'}})
      .then(this.update1)
      .catch(function(error) {
        console.log(error);
      });

  };

  timer = ( label, tim, ticks ) => {
    if( tim['started'] === 0){
      tim['started'] = ticks;
    }
    const dir = tim['red'] < tim['yellow'];
    const on = tim['state'] === 'on';
    const max = tim['max']; //Math.round(tim['orange']/2)*4;
    var value = ( on ? Math.round((ticks - tim['started']) * 100 / tim['units']) / 100 : 0 );
    value = max > 100 ? Math.round(value) : value;
    //console.log('now',ticks/1000);
    return (
      <td id={label} key={label}>
        <div onClick={this.onClickTimer.bind(this,label,ticks)}>
          <Chart width={"150px"} height={"150px"} chartType="Gauge" loader={"Loading data ..."}
            data={[['Label', 'Value'], [(on?label:'#'+label+'#'), value ]]}
            options={{  minorTicks:(on?4:0),
              redFrom: (on ? (dir?0:tim['red']) : 0), redTo: (on ? (dir?tim['red']:max) : 0),
              yellowFrom: (on ? (dir?tim['red']:tim['yellow']) : 0), yellowTo: (on ? (dir?tim['yellow']:tim['red']) : 0),
              greenFrom: (on ? (dir?tim['yellow']:0) : 0), greenTo: (on ? (dir?max:tim['yellow']) : 0),
              min: 0, max: (on ? max : 1)
            }}
            rootProps={{ 'data-test': '2' }}
          />
        </div>
        <Button onClick={this.move.bind(this,label,'-')} value='-'>-</Button>&nbsp;
        <Button onClick={this.move.bind(this,label,'+')} value='+'>+</Button>
    </td>
  )}

  render(){
    //const report_style = { position: 'static', float: 'left', width: '100%' }
    var timer_components = [];
    //console.log('state render', this.state);
    //console.log('timers2', Object.keys(this.state.timers));
    //for( var key of Object.keys(this.state.timers)){
        //console.log('key', key);
    //};
    var rows = [];
    if( this.state.ready ){
      const now = new Date();
      Object.keys(this.state.timers).sort().forEach( t => {
          timer_components.push( this.timer( t, this.state.timers[t], now.getTime() ));
      });
      for( var i=0; i<timer_components.length; i+=2){
        const second = i<timer_components.length ? timer_components[i+1] : <td key={i}></td>;
        rows.push(
          <tr key={i}>{timer_components[i]}{second}</tr>
        );
      }
    }else{
      rows = <tr><td>Loading . . .</td></tr>;
    }

    return (
      <table>
        <tbody>
          {rows}
        </tbody>
      </table>
    );
  }
}

export default Timers;
