import React, {Component} from 'react';
//import {Image} from 'react-native';
import axios from 'axios'
//import {Button} from 'react-bootstrap';

class Today extends Component{
  state = {
    photos: []
  };

  serviceURL = () => {
    const url = process.env.NODE_ENV==="development" ? window.location.protocol+'//'+window.location.hostname+':8000' : "";
    return url;
  };

  update = (response) => {
    //console.log('response', response.data);
    this.setState( { photos: response.data.today } );
  };

  refresh = () => {
    var s = window.location.href;
    var a = s.split('/');
    s = a[a.length-1];
    if(s === 'today'){
      s = '';
    }else{
      s = '/'+s;
    }
    console.log(s)
    axios.get(this.serviceURL()+'/photos-api/day'+s,
        {headers: {'Access-Control-Allow-Origin': ' *'}})
        .then(this.update)
        .catch(function(error) {
          console.log(error);
        }
      );
  };

  componentDidMount() {
    this.refresh();
  };

  images = () => {
    const img_style = {
      //height: '100%',
      //minHeight: '100%',
      //width: '50%',
      height: '100%',
      //align: 'center',
      //objectFit: 'cover',
      //display: 'block'
    };
    const imgs = []
    const photos = this.state.photos;
    photos.forEach( i => {
      imgs.push(
          <a
            href={"https://photos.google.com/search/"+i.filename}
            target="_blank"
            rel="noopener noreferrer"
            key={i.imgId}
          >
            <img src={i.url} style={img_style}
              alt={i.imgId} title={i.albumTitle+" // "+i.date}
            />&nbsp;
          </a>
      );
    });
    return (
      <div>
        {imgs}
      </div>
    );
  }

  render(){
    //console.log(this.state);
    //console.log('===x=',window.location.pathname);
    return (
      <div>
        {this.images()}
      </div>
    );
  }
}

export default Today;
