import React, {Component} from 'react';
//import {Image} from 'react-native';
import axios from 'axios'
import {Button} from 'react-bootstrap';

class Orphans extends Component{
  state = {
    photos: []
  };

  serviceURL = () => {
    const url = process.env.NODE_ENV==="development" ? window.location.protocol+'//'+window.location.hostname+':8000' : "";
    return url;
  };

  update = (response) => {
    //console.log('response', response.data);
    this.setState( { photos: response.data.orphans } );
  };

  refresh = () => {
    axios.get(this.serviceURL()+'/photos-api/orphans',
        {headers: {'Access-Control-Allow-Origin': ' *'}})
        .then(this.update)
        .catch(function(error) {
          console.log(error);
        }
      );
  };

  nothing = (response) => {
  };

  startOrphans = () => {
    axios.get(this.serviceURL()+'/photos-api/startorphans',
        {headers: {'Access-Control-Allow-Origin': ' *'}})
        .then(this.nothing)
        .catch(function(error) {
          console.log(error);
        }
      );
  };

  componentDidMount() {
    this.refresh();
  };

  images = () => {
    const img_style = {
      //height: '100%',
      //minHeight: '100%',
      //width: '50%',
      height: '100%',
      //align: 'center',
      //objectFit: 'cover',
      //display: 'block'
    };
    const imgs = []
    const photos = this.state.photos;
    photos.forEach( i => {
      imgs.push(
          <a
            href={"https://photos.google.com/search/"+i.filename}
            target="_blank"
            rel="noopener noreferrer"
            key={i.imgId}
          >
            <img src={i.url} style={img_style} alt={i.imgId}/>&nbsp;
          </a>
      );
    });
    return (
      <div>
        {imgs}
      </div>
    );
  }

  render(){
    //console.log(this.state);
    return (
      <div>
        {this.images()}
        <Button onClick={this.startOrphans}>Start Orphans</Button>
      </div>
    );
  }
}

export default Orphans;
